import "./App.css";
import { useState, useEffect } from "react";
import "react-image-crop/dist/ReactCrop.css";
import ReactSlider from "react-slider";
import { ChromePicker  } from "react-color";

function ImgComponent(props) {
  const [showMenu, setShowMenu] = useState(false);

  const [data, setData] = useState({
    id: props.id,
    img: props.img,
    text: props.text,
    color: props.color,
    fontsize: props.fontsize,
    fontweight: props.fontweight,
    shadow: props.shadow,
    shadowcolor: props.shadowcolor,
    blur: props.blur,
  });

  const [id, setId] = useState(props.data.id);
  const [img, setImg] = useState(props.data.img);
  const [text, setText] = useState(props.data.text);
  const [color, setColor] = useState(props.data.color);
  const [fontsize, setFontsize] = useState(props.data.fontsize);
  const [fontweight, setFontWeight] = useState(props.data.fontweight);
  const [shadow, setShadow] = useState(props.data.shadow);
  const [shadowcolor, setShadowColor] = useState(props.data.shadowcolor);
  const [blur, setBlur] = useState(props.data.blur);
  const [delay, setDelay] = useState(props.data.delay);

  useEffect(() => {
    setData({
      id: id,
      img: img,
      text: text,
      color: color,
      fontsize: fontsize,
      fontweight: fontweight,
      shadow: shadow,
      shadowcolor: shadowcolor,
      blur: blur,
      delay: delay,
    });
    props.onChange({
      id: id,
      img: img,
      text: text,
      color: color,
      fontsize: fontsize,
      fontweight: fontweight,
      shadow: shadow,
      shadowcolor: shadowcolor,
      blur: blur,
      delay: delay,
    });
  }, [id, img, text, color, fontsize, fontweight, shadow, shadowcolor, blur, delay]);


  return (
    <div
      className="img"
      style={{
        backgroundImage: `url('${props.data.img}')`,
        fiilter: blur + "px",
      }}
    >
      <button
        className="edit"
        onClick={() => {
          setShowMenu(!showMenu);
          props.onChange(data);
        }}
      >
        <i className="fa fa-cog" />
      </button>

      {showMenu ? (
        <div className="inner">
          <div className="ver">
            <div
              className="editing-img-small"
              style={{
                backgroundImage: `url('${props.data.img}')`,
              }}
            ></div>
            <p className="bold">Edit image text</p>
            <button
              className="close-btn"
              onClick={() => {
                setShowMenu(!showMenu);
              }}
            >
              <i className="fa fa-times"></i>
            </button>
          </div>
          <hr className="mt-2 mb-2" />
          <div className="row">
            <div className="col-lg-5">
              <small className="">Text Content</small>
              <input
                type="text"
                placeholder="Add text"
                className="text"
                onChange={(e) => {
                  setText(e.target.value);
                  props.onChange(data);
                }}
                value={text}
              />
              <small className="">Font Size</small>
              <input
                type="number"
                placeholder="Font size (px)"
                className="text"
                onChange={(e) => {
                  setFontsize(e.target.value);
                }}
                value={fontsize}
              />
              <small className="">Font Weight</small>
              <ReactSlider
                className="slider "
                thumbClassName="slider-thumb"
                trackClassName="slider-track"
                marks={[100, 200, 300, 400, 500, 600, 700, 800, 900]}
                markClassName="slider-mark"
                withTracks={true}
                min={100}
                max={900}
                step={100}
                defaultValue={fontweight}
                renderThumb={(styles, state) => {
                  //do something
                  console.log(state.valueNow);
                  setFontWeight(state.valueNow);

                  return <div {...styles}>{state.valueNow}</div>;
                }}
              />
              <small className="">Text Shadow</small>
              <input
                type="text"
                placeholder="Color (e.g #000)"
                className="text"
                onChange={(e) => {
                  setShadowColor(e.target.value);
                }}
                value={shadowcolor}
              />
              <ReactSlider
                className="slider "
                thumbClassName="slider-thumb"
                trackClassName="slider-track"
                markClassName="slider-mark"
                withTracks={true}
                min={0}
                max={100}
                defaultValue={shadow}
                renderThumb={(styles, state) => {
                  //do something
                  setShadow(state.valueNow);
                  return <div {...styles}>{state.valueNow}</div>;
                }}
              />
              
              {/* an error with screen to svg where it distorts the blurred background */}
              {/* <small className="" hidden>Image Blur</small>
              <ReactSlider
                
                className="slider "
                thumbClassName="slider-thumb"
                trackClassName="slider-track"
                markClassName="slider-mark"
                withTracks={true}
                min={0}
                max={100}
                defaultValue={blur}
                renderThumb={(styles, state) => {
                  //do something
                  setBlur(state.valueNow);
                  return <div {...styles}>{state.valueNow}</div>;
                }}
              /> */}



              

              {/* üêû bug on delete it updates the images array state but removes the last item of the .map array   */}
              {/* üåü Fixed:
                  by giving the ImgComp key={each.id} which is 
                  the unique id of the image object not the index of
                  an array 
              */}
              <button
                className="remove-btn mt-3"
                onClick={() => {
                  props.delete(id);
                  setShowMenu(false)
                }}
                
              >
                <i className="fa fa-trash"></i> Delete Image
              </button>
            </div>
            <div className="col-lg-7">
              <div className="mb-3">
                <label className=" label">Delay (ms)</label>
                <input
                  type="number"
                  value={delay}
                  onChange={(e) => {
                    setDelay(e.target.value);
                    props.onChange(data);
                  }}
                />
              </div>
              <ChromePicker
                color={color}
                onChange={(color) => {
                  setColor(color.hex);
                }}
                className="img-fluid"
              />
            </div>
          </div>
        </div>
      ) : (
        ""
      )}
      <input
        type="text"
        placeholder="Add text"
        className=" text-second"
        onChange={(e) => {
          setText(e.target.value);
          props.onChange(data);
        }}
        value={text}
      />
    </div>
  );
}


export { ImgComponent }; 